import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			iconName:'arrow-down',
			tabLook:'查看',
			tabInfoShow:false,
			active:0,
			time: 30 * 60 * 60 * 1000,
			songdan: '../../static/delivery/songdan.png',
			jiedan: '../../static/delivery/jiedanset1.png',
			daliveryData: 1,
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
			],
			deliverylist: [{
				state: 1,
				time: '15:12',
				countDown: 30 * 60 * 60 * 1000,
				shopName: '麦田圈官网旗舰店',
				state: 5,
				steps: [{
					text: '步骤一',
					desc: '描述'
				},
				{
					text: '步骤二',
					desc: '描述'
				}],
				info: {
					a: 20,
					b: '麦田圈预单',
					c: '预'
				},
				msg: {
					content:'海苔沙拉炒饭',
					price:'15.00',
					num:1,
					oddNum:'6546545646464546',
					sorce:'校内订单',
					beizhu:'好的',
					biaoshi:25,
					time:'2019-11-14 12:12:00'
				}
			}]
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = 0;
		// this.loadData();
		if (options.state == 1) {
			// this.loadData()
		}
	},
	methods: {
		look(){
			if(!this.tabInfoShow){
				this.tabInfoShow = true;
				this.tabLook = '收起';
				this.iconName='arrow-up';
			}else{
				this.tabInfoShow = false;
				this.tabLook = '查看';
				this.iconName='arrow-down';
			}
			
		},
		// 跳转页面
		sd(e) {
			if (e == 2) {
				this.songdan = '../../static/delivery/songdan2.png';
				this.jiedan = '../../static/delivery/jiedanset1.png';
				this.daliveryData = 2;
				uni.navigateTo({
					url: 'set'
				})
			}
		},
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			// navItem.loadingType = 'loading';

			let orderList = Json.deliverylist.filter(item => {
				//添加不同状态下订单的表现形式
				item = Object.assign(item, this.orderStateExp(item.state));
				//演示数据所以自己进行状态筛选
				if (state === 0) {
					//0为全部订单
					return item;
				}
				return item.state === state
			});
			orderList.forEach(item => {
				navItem.orderList.push(item);
			})
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			this.$set(navItem, 'loaded', true);

			//判断是否还有数据， 有改为 more， 没有改为noMore 
			// navItem.loadingType = 'more';

		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		// 评价
		evaluate(item) {
			console.log(item)
		},
		// 再次购买
		againBuy(item) {
			console.log(item)
		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip,
					stateTipColor
				})

				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val => val.id === item.id);
				index !== -1 && list.splice(index, 1);

				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待支付';
					break;
				case 4:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待收货';
					break;
				case 3:
					stateTip = '待评价';
					break;
				case 5:
					stateTip = '';
					break;
				case 6:
					stateTip = '交易成功';
					break;
				case 7:
					stateTip = '已完成';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		},
		//订单详情
		orderDetails(item) {
			if (item.state != 3) {
				uni.navigateTo({
					url: 'orderDetails?status=' + item.state
				})
			}

		},
		// 物流信息
		logisticsTap() {
			uni.navigateTo({
				url: 'logistics'
			})
		}
	},
}
